var sss = 'data:image/png;base64,'

for(let i = 1 ; i < randomNumber ; i++){
luckyvalue += "<img src="+sss+" />";
}
function test(){
  localStorage.setItem('lucky',luckyvalue);
  localStorage.setItem('value',1);
  document.getElementById('testbtn').style.display='none';
  document.getElementById('test').innerHTML =   localStorage.getItem('lucky',luckyvalue);
}
if(localStorage.value == 1){
  window.onload = function(){
    setTimeout("",5000)
    document.getElementById('test').innerHTML =   localStorage.getItem('lucky',luckyvalue);
    document.getElementById('testbtn').style.display='none';
    localStorage.setItem('value',);
  }
  
}else{
  window.onload = function(){
    document.getElementById('testbtn').style.display='block';
  }

}

